<template>
  <div class="container">
    <div v-if="loading">Loading ...</div>
    <div v-else class="relative my-6">
      <div class="flex border primary-border-color">
        <div class="w-7/12 px-12">
          <img :src="url" alt="">
        </div>
        <div class="w-5/12 border-l primary-border-color">
          <div v-if="step === 0">
            <div class="p-4 text-center">
              <h3 class="text-3xl mb-2">Personalization Options</h3>
              <p class="mb-3 font-semibold text-sm">Personalize your gift by adding a specially curated Baudville graphic sentiment, your company logo, and/or your own personal message.</p>
              <h3 class="text-3xl">Lets Get Started!</h3>
            </div>

            <StepButton v-for="(info, index) in product.steps" @stepChange="step = step + index + 1" :key="index" :step_number="index + 1" :name="info.name"/>
            <StepButton v-if="product.logo_upload_type > 0" @stepChange="step = product.steps.length + 1"  :step_number="product.steps.length + 1" name="Add Logo or Artwork"/>
            <StepButton @stepChange="step = product.steps.length + 2"  :step_number="product.steps.length + 2" name="Approval"/>
          </div>

          <div v-for="(item, index) in product.steps"  :key="index">
            <div v-if="step === index + 1" class="h-full relative">
              <StepHeading @back="step = 0" :name="item.name"/>

              <StepNavigation @next="step = step + 1" @back="step = step - 1"/>

              <div class="p-6 bg-gray-100 configurator-body">
                <div class="mb-6" v-for="option in item.options" :key="option.id">
                  <div :class="option.type === 0 ? 'flex' : ''">
                    <div :class="option.type === 0 ? 'w-28' : ''">
                      <p :class="option.type === 0 ? 'mt-2' : 'mb-3'"><label :for="'option-' + option.id" class="font-bold text-sm">{{option.label}}</label></p>
                    </div>

                    <div class="w-full">
                      <div v-if="option.type === 0">
                        <div v-for="(item, index) in 6"  :key="index">
                          <div v-if="option.field === 'line' + item">
                            <input :maxlength="customization.lines[index].bigActive ? customization.lines[index].bigSize : option.max_char" v-model="customization.lines[index].line" type="text" @input="dynamicImage" class="baudville-input">
                            <p class="text-xs mt-1">{{ (customization.lines[index].bigActive ? customization.lines[index].bigSize : option.max_char) - customization.lines[index].line.length }} Characters Remaining</p>
                          </div>
                        </div>
                      </div>





                      <select v-if="option.type === 1" v-model="sentiment" @change="sentimentChange" class="w-full border-gray-200 px-4 py-2 focus:ring-0 focus:outline-none" :id="'option-' + option.id">
                        <option v-for="setting in option.settings" :key="setting.id" :value="setting">{{ setting.name }}</option>
                      </select>
                    </div>

                    <div  v-for="(item, index) in 6" :key="index" :style="{marginTop: '2px'}">
                      <div v-if="option.field === 'line' + item" class="min-w-max ml-2">
                        <div class="rounded border-gray-100 bg-white flex secondary-color uppercase font-bold">
                          <button @click="bigFontToggle(index, false, option.max_char_big)" v-bind:class="customization.lines[index].bigActive === false ? 'bg-gray-300' : 'hover:bg-gray-50'" class="px-3 py-1 rounded-l">A</button>
                          <button @click="bigFontToggle(index, true, option.max_char_big)" v-bind:class="customization.lines[index].bigActive === true ? 'bg-gray-300' : 'hover:bg-gray-50'" class="text-xl px-3 py-1  rounded-r">A</button>
                        </div>
                      </div>

                    </div>
                  </div>





                  <div v-if="sentiment && step === 1" v-html="sentiment.content" class="text-center mt-8"></div>
                </div>
              </div>
            </div>


          </div>

          <div v-if="step === product.steps.length + 1 && product.logo_upload_type > 0" class="relative h-full">
            <StepHeading @back="step = 0" name="Add Logo or Artwork"/>

            <StepNavigation @next="step = step + 1" @back="step = step - 1"/>

            <div class="p-6 bg-gray-100 configurator-body">
              <h3 class="flex uppercase text-2xl items-end mb-2">Add your logo or artwork <span class="font-sans normal-case text-sm primary-color mb-1 ml-2">(Optional)</span></h3>
              <p class="font-semibold mb-6">Customize with your organization's logo or graphic. Minimum $35 setup fee; free with orders over $100! <a href="https://www.baudville.com/products/configurator/%7Bspecs_path%7D">Read Artwork Specifications</a></p>

              <div class="content text-xs primary-border-color border-b pb-3 mb-6">
                <p>Preferred File Type: .eps, .ai, .pdf</p>
                <p>Other accepted file types MUST BE at least 4in x 4in and 300dpi .psd, .png, .jpg</p>

                <p>For best results on your logo reproductions, use a black and white logo graphic.</p>

                <p>Please wait for your logo to be fully uploaded before proceeding to the next step. A check mark will appear and your logo will be placed on the product to indicate your upload is complete.</p>
              </div>

              <div class="font-bold mb-6">
                <p class="mb-4"><input id="upload-logo[no]" type="radio" v-model="uploadLogo" :value="false"> <label for="upload-logo[no]" class="cursor-pointer">No Thanks</label></p>
                <p><input id="upload-logo[yes]" type="radio" v-model="uploadLogo" :value="true"> <label for="upload-logo[yes]" class="cursor-pointer">Yes, I would like to upload a logo</label></p>
              </div>

              <div v-if="uploadLogo" class="border-2 border-gray-200 p2">
                <div class="bg-white">
                  <vue-dropzone ref="myVueDropzone" id="dropzone" :options="dropzoneOptions"></vue-dropzone>
                </div>
              </div>


            </div>
          </div>

          <div v-if="step === product.steps.length + 2" class="">
            <StepHeading @back="step = 0" name="Approval"/>

            <div class="p-6 bg-gray-100 configurator-body">
              <p class="font-bold mb-2">Enter any special instructions you have for your item(s) in the box below. If you have a due date for your order, please enter it here. Please note: as it is our busy season, we cannot guarantee due dates, but we will always try our best!</p>
              <label class="font-bold block w-full mb-1" for="">Special Instructions</label>
              <input class="w-full border border-gray-200 px-4 py-2" type="text">
            </div>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import vue2Dropzone from 'vue2-dropzone'
import 'vue2-dropzone/dist/vue2Dropzone.min.css'
import StepNavigation from "./components/StepNavigation";
import StepButton from "./components/StepButton";
import StepHeading from "./components/StepHeading";
import axios from "axios";

export default {
  name: "configurator-baudville",
  components: {
    StepHeading,
    StepButton,
    StepNavigation,
    vueDropzone: vue2Dropzone
  },
  data() {
    return {
      dropzoneOptions: {
        url: 'https://httpbin.org/post',
        thumbnailWidth: 150,
        maxFilesize: 0.5,
        addRemoveLinks: true,
        headers: { "My-Awesome-Header": "header value" }
      },
      product: '',
      loading: true,
      step: 0,
      current: '',

      url: '',
      uploadLogo: false,
      sentiment: '',
      customizations: [],
      customization: {
        sentiment: '',
        lines: [
          {
            line: '',
            size: '',
            bigSize: '',
            bigActive: false
          },
          {
            line: '',
            size: '',
            bigSize: '',
            bigActive: false
          },
          {
            line: '',
            size: '',
            bigSize: '',
            bigActive: false
          },
          {
            line: '',
            size: '',
            bigSize: '',
            bigActive: false
          },
          {
            line: '',
            size: '',
            bigSize: '',
            bigActive: false
          },
          {
            line: '',
            size: '',
            bigSize: '',
            bigActive: false
          }
        ]
      }
    }
  },
  mounted() {
    axios.get('https://baudville.site-under-dev.com/api/product/1').then(response => {
      this.product = response.data;
      this.sentiment = this.product.steps[0].options[0].settings[0];
      this.customization.sentiment = this.sentiment.field;


      for (let i = 0; i < 6; i++) {
        this.customization.lines[i].size = this.product.line_size_normal;
      }

      const lineCheck = ['line1', 'line2', 'line3', 'line4', 'line5', 'line6'];
      this.product.steps.forEach(step => {
        step.options.forEach(option => {
          if (lineCheck.includes(option.field)) {
            const index = lineCheck.indexOf(option.field);
            this.customization.lines[index].bigSize = option.max_char_big;
          }
        })
      });

      this.dynamicImage();

      this.loading = false;
    }).catch(error => {
      console.log(error)
    });

  },
  methods: {
    dynamicImage() {
      this.url = `https://s7d7.scene7.com/is/image/Baudville/${this.product.scene7_image_id}_TEMP?$fn=&$linelp=&layer=0&src=is{Baudville/${this.product.scene7_image_id}_FRONT}&layer=1&hide=0&op_colorize=939393&src=is{Baudville/${this.customization.sentiment}}&layer=2&op_colorize=939393&layer=3&op_colorize=939393&layer=4&op_colorize=939393`;

      this.url += `&$line1=${this.customization.lines[0].line}&$line1fs=${this.customization.lines[0].size}`;
      this.url += `&$line2=${this.customization.lines[1].line}&$line2fs=${this.customization.lines[1].size}`;
      this.url += `&$line3=${this.customization.lines[2].line}&$line3fs=${this.customization.lines[2].size}`;
      this.url += `&$line4=${this.customization.lines[3].line}&$line4fs=${this.customization.lines[3].size}`;
      this.url += `&$line5=${this.customization.lines[4].line}&$line5fs=${this.customization.lines[4].size}`;
      this.url += `&$line6=${this.customization.lines[5].line}&$line6fs=${this.customization.lines[5].size}`;
    },
    sentimentChange() {
      this.customization.sentiment = this.sentiment.field;
      this.dynamicImage();
    },
    bigFontToggle(index, action, max_char_big) {
      if(action === false && this.customization.lines[index].bigActive) {
        this.customization.lines[index].bigActive = false;
      } else if(action === true && !this.customization.lines[index].bigActive) {
        this.customization.lines[index].bigActive = true;
        this.customization.lines[index].line = this.customization.lines[index].line.substring(0, max_char_big);
      }

      this.dynamicImage();
    }
  }
}
</script>

